/* src/css/product/ProductInfoBox.css */

.info-box {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/*  */
.product-name {
  font-size: 28px;
  font-weight: 700;
  color: #222;
  margin: 0;
  line-height: 1.3;
}

/*  */
.product-rating {
  font-size: 16px;
  color: #555;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 4px;
}

/*   */
.tag-container {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.tag-badge {
  font-size: 12px;
  background: #fff;
  color: #666;
  padding: 4px 10px;
  border: 1px solid #ddd;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.2s;
}

.tag-badge:hover {
  border-color: #333;
  color: #333;
}

/*  */
.product-price {
  font-size: 24px;
  font-weight: 800;
  color: #111;
  padding-top: 20px;
  border-top: 1px solid #f4f4f4;
  margin: 0;
}

/*   () */
.product-select {
  width: 100%;
  padding: 14px;
  font-size: 15px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background-color: #fff;
  outline: none;
  cursor: pointer;
}

.product-select:focus {
  border-color: #333;
}

/* --- []    --- */
.qty-control {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 150px; /*    */
  height: 45px; /* []   45px  ( 2 ) */
  background-color: #fff;
  overflow: hidden; /*       */
}

.qty-btn {
  width: 45px; /*   =  ( ) */
  height: 100%; /*  (45px)   */
  background-color: #f8f8f8; /*    */
  border: none;
  font-size: 20px; /* +/-    */
  color: #333;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.2s;
}

.qty-btn:hover {
  background-color: #eee;
}

.qty-input {
  flex: 1; /*    */
  height: 100%; /*     */
  text-align: center;
  border: none;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  font-size: 16px; /*   */
  font-weight: 600;
  width: 100%;
  outline: none;
  -moz-appearance: textfield; /* Firefox  */
  appearance: textfield;      /*   */
}

/* Chrome, Safari, Edge, Opera (Spin Button)  */
.qty-input::-webkit-outer-spin-button,
.qty-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/*    */
.total-price-box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 10px;
  padding-top: 20px;
  border-top: 2px solid #333;
  gap: 12px;
}

.total-price-label {
  font-size: 15px;
  font-weight: 500;
  color: #555;
}

.total-price-value {
  font-size: 28px;
  font-weight: 800;
  color: #111;
}

/*   */
.btn-group {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

/*   */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}