/*    */
.shop-search-container {
  width: 100%;
  margin-bottom: 20px; /*   /    */
  display: block; /*       */
}

/*   */
.search-input-wrapper {
  position: relative;
  width: 100%;
  max-width: 100%; /*    ( ) */
}

/*    */
.shop-search-input {
  width: 100%;
  height: 48px; /*     */
  padding: 0 50px 0 15px; /*    ,    */
  border: 1px solid #e1e1e1; /*    */
  border-radius: 24px;
  font-size: 14px;
  color: #333;
  outline: none;
  background-color: #fff;
  transition: border-color 0.2s;
  box-sizing: border-box;
}

/*    */
.shop-search-input:focus {
  border-color: #333; /*    */
}

/*      */
.shop-search-icon {
  position: absolute;
  right: 15px; /*   */
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5; /*    */
  transition: opacity 0.2s;
}

.shop-search-input:focus + .shop-search-icon {
  opacity: 1; /*      */
}

/*  SVG   ( ) */
.shop-search-icon svg {
  width: 20px;
  height: 20px;
}