/* --- 1.   (OrderPage.css ) --- */
/*     ,       */
.payment-page {
  width: 100%;
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  color: #333;
}
.payment-page h1 {
  font-size: 2rem;
  margin-top: 40px;
  margin-bottom: 20px;
}
/*    (  ) */
.payment-page h2 {
  font-size: 1.5rem;
  margin-top: 30px;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  text-align: left;
}
/*     */
.payment-page h3 {
  font-size: 1.25rem;
  margin-bottom: 20px;
  text-align: left;
}
/*        Flex  */
.payment-content-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
/*     ( ,     ) */
.payment-details {
  flex-grow: 1; /*      */
}

/* --- 2.    (OrderPage.css ) --- */
/* /      */
.checkout-progress {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
}
/*     */
.step-item { display: flex; flex-direction: column; align-items: center; color: #ccc; font-weight: bold; }
/*   (   )  */
.step-item .step-icon { width: 30px; height: 30px; border-radius: 50%; background-color: #eee; 
  color: #ccc; display: flex; justify-content: center; align-items: center; font-size: 1rem; 
  margin-bottom: 8px; }
/*     */
.step-item.active { color: #333; }
.step-item.active .step-icon { background-color: #333; color: #fff; }
/*    */
.step-item.complete { color: #00c73c; }
.step-item.complete .step-icon { background-color: #00c73c; color: #fff; }
/*     */
.step-connector { flex-grow: 1; height: 2px; background-color: #eee; margin: 0 10px; 
  position: relative; bottom: 15px; }

/* --- 3.    --- */
/*      */
.payment-method-options {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
/*       */
.method-option {
  display: flex;
  align-items: center;
  padding: 18px 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
}
/*      */
.method-option .radio-icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ccc;
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease;
}
/*        */
.method-option .radio-icon::after {
  content: '';
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #333;
  display: none; /*   */
  transition: all 0.2s ease;
}
/*        */
.method-option.selected {
  border-color: #333;
  border-width: 2px;
  padding: 17px 19px; /*      */
}
.method-option.selected .radio-icon {
  border-color: #333;
}
/*        */
.method-option.selected .radio-icon::after {
  display: block; 
}

/* --- 4. /  (OrderPage.css ) --- */
/*         */
.card-info-section, .points-section {
  margin-top: 20px;
}
/*         */
.card-form, .points-form {
  padding: 20px;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  background-color: #fafafa;
}
/* h2         */
.payment-details h2 + .card-info-section,
.payment-details h2 + .points-section {
  margin-top: 0;
}
.card-form .form-group, .points-form.form-group-with-button {
  margin-bottom: 20px;
}
/*         */
.card-form .form-group:last-child,
.points-form.form-group-with-button:last-child {
  margin-bottom: 0;
}

/*    */
.card-form label {
  display: block;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 8px;
  text-align: left;
}
/* /     */
.card-form input[type="text"], .points-form input[type="number"] {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}
/*          */
.card-form .form-group-half { display: flex; gap: 20px; }
.card-form .form-group-half .form-group { width: 50%; }

/*   */
/*         (:   ) */
.form-group-with-button {
  display: flex;
  align-items: center; /*      */
  gap: 10px;
}
.form-group-with-button input {
  flex-grow: 1;
}
/*    (      ) */
.btn-outline {
  padding: 12px 20px;
  font-size: 1rem;
  font-weight: 600;
  border: 1px solid #555;
  background-color: #fff;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap; /*    */
}
/*      */
.points-info {
  font-size: 0.9rem;
  color: #666;
  text-align: left;
  margin-top: 10px;
}

/* --- 5.   --- */
/*    (  ) */
.agreement-section {
  margin-top: 30px;
  padding-top: 20px;
  border-top: 1px solid #eee;
  text-align: left; 
}
/*     */
.agreement-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
/*   */
.agreement-item input[type="checkbox"] {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  flex-shrink: 0; 
}
.agreement-item label {
  font-size: 0.95rem;
  color: #333;
}
/*      */
.agreement-link {
  font-size: 0.9rem;
  color: #555;
  text-decoration: underline;
  margin-left: 30px; /*    */
}

/* --- 6.   (OrderPage.css ) --- */
/*     */
.order-summary-sidebar {
  width: 320px;
  flex-shrink: 0;
}
/*      */
.summary-box {
  border: 1px solid #eee;
  background-color: #fafafa;
  padding: 24px;
  border-radius: 4px;
  text-align: left;
}
/*     (  Flex ) */
.summary-row {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
  color: #555;
  margin-bottom: 15px;
}
/*       () */
.summary-row.discount {
  color: #d9534f; 
}
/*      (  ) */
.summary-total {
  display: flex;
  justify-content: space-between;
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #ddd;
}
/*    ( ) */
.btn-primary { width: 100%; padding: 14px; font-size: 1rem; font-weight: 600; 
  color: #fff; background-color: #333; border: none; border-radius: 4px; 
  cursor: pointer; margin-bottom: 10px; }
/*    ( ) */
.btn-secondary { width: 100%; padding: 14px; font-size: 1rem; font-weight: 600; 
  color: #333; background-color: #fff; border: 1px solid #ddd; border-radius: 4px; 
  cursor: pointer; }
/*       */
.summary-note {
  font-size: 0.8rem;
  color: #888;
  margin-top: 20px;
  line-height: 1.5;
  text-align: center;
}