/* ---    --- */
.slider_container {
    position: relative;
    margin-bottom: 40px;
}

.slide {
    height: 450px;
    margin: 20px 0;
    background-size: cover;
    background-position: center;
    position: relative;
    outline: none;
    display: flex !important;
    align-items: center;
    justify-content: center;
}

/* ---   () --- */
.slider_container .slick-prev,
.slider_container .slick-next {
    z-index: 10;
    width: 40px;
    height: 40px;
}
.slider_container .slick-prev { left: 30px; }
.slider_container .slick-next { right: 30px; }

.slider_container .slick-prev:before,
.slider_container .slick-next:before {
    font-size: 40px;
    color: #fff;
    opacity: 0.6;
    transition: opacity 0.2s;
    text-shadow: 0 1px 3px rgba(0,0,0,0.3);
}

.slider_container .slick-prev:hover:before,
.slider_container .slick-next:hover:before {
    opacity: 1;
}

/* ---    () --- */
.banner-content {
    width: 100%;
    max-width: 1200px;
    padding: 0 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/*   Modifiers */
.banner-content.align-right { align-items: flex-end; text-align: right; padding-right: 120px; }
.banner-content.align-left { align-items: flex-start; text-align: left; padding-left: 120px; }

/*   () */
.banner-content h3 {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 20px;
    line-height: 1.3;
}

.banner-content p {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 30px;
}

/* --- Glass Box  (/  ) --- */
.glass-box {
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(5px);
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 10px 30px rgba(0,0,0,0.05);
    max-width: 500px;
}

/*   */
.banner-content.align-right .glass-box { margin-right: 30px; text-align: right; }
.banner-content.align-right .glass-box.text-left { text-align: left; } /*  */


/* ---    --- */

/* 1.  () */
.theme-skincare h3 { color: #003366; }
.theme-skincare p { color: #4a6fa5; }

/* 2.  (  + ) */
.theme-makeup h3 { color: #222; text-shadow: 2px 2px 10px rgba(255, 255, 255, 0.5); }
.theme-makeup p { color: #555; }

/* 3.  () */
.theme-body h3 { color: #2e4a35; }
.theme-body p { color: #555; }

/* 4.  () */
.theme-homme h3 { color: #fff; }
.theme-homme p { color: #cccccc; }


/* ---   () --- */
.banner-btn {
    display: inline-block;
    padding: 10px 24px;
    font-size: 15px;
    font-weight: 700;
    text-decoration: none;
    transition: all 0.2s;
    border: 1px solid transparent;
}
.banner-btn:hover { transform: translateY(-2px); }

/*   Modifiers */
.btn-round { border-radius: 30px; } /*   */
.btn-square { border-radius: 2px; } /*   */

/*   Themes */
.btn-skincare {
    color: #003366;
    border-color: #003366;
    background: transparent;
}
.btn-skincare:hover { background: #003366; color: #fff; }

.btn-makeup {
    background: #fff;
    color: #d66d75;
    border-color: #ffebeb;
    box-shadow: 0 4px 10px rgba(214, 109, 117, 0.2);
}
.btn-makeup:hover { background: #d66d75; color: #fff; }

.btn-body {
    background: #2e4a35;
    color: #fff;
}
.btn-body:hover { background: #3e6347; }

.btn-homme {
    background: #d9d9d9;
    color: #000;
}
.btn-homme:hover { background: #fff; }


/* ---   --- */
@media (max-width: 768px) {
    .banner-content {
        padding: 0 20px !important;
        align-items: center !important;
        text-align: center !important;
    }
    
    .glass-box {
        margin: 0 !important;
        width: 100%;
        text-align: center !important;
        background-color: rgba(255,255,255,0.8);
    }

    .banner-content h3 { font-size: 28px; }
    .banner-content p { font-size: 14px; }
    
    .slide { 
        align-items: flex-end; /*      */
        padding-bottom: 40px; 
    }
}