.notice-container {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 60px 20px;
  min-height: 70vh;
  font-family: "Pretendard", sans-serif;
  box-sizing: border-box;
}

.notice-header {
  text-align: center;
  margin-bottom: 50px;
}

.notice-header h1 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #222;
}

.notice-header p {
  font-size: 16px;
  color: #666;
}

.notice-list {
  border-top: 2px solid #222;
}

/*   */
.notice-list-header {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid #ddd;
  background-color: #fafafa;
  font-weight: 600;
  color: #333;
  text-align: center;
  font-size: 15px;
}

/*   */
.notice-item {
  border-bottom: 1px solid #eee;
}

/*   (  ) */
.notice-summary {
  display: flex;
  padding: 20px 0;
  cursor: pointer;
  transition: background-color 0.2s;
  align-items: center;
  text-align: center;
}

.notice-summary:hover {
  background-color: #f9f9f9;
}

/*    */
.col-id { width: 80px; color: #666; }
.col-title { 
  flex: 1; 
  text-align: left; 
  padding: 0 20px; 
  color: #222;
  font-weight: 500;
}
.col-date { width: 120px; color: #888; font-size: 14px; }

/*    */
.arrow-icon {
  width: 60px;
  height: 100%;
  min-height: 24px;
  color: #bbb; /*    */
  
  display: flex;
  align-items: center;
  justify-content: center;
  
  /*   */
  transition: transform 0.3s ease, color 0.2s;
  transform-origin: center center;
}

.arrow-icon svg {
  display: block; /*     */
}

/*     */
.notice-item.active .arrow-icon {
  transform: rotate(180deg);
  color: #222; /*   */
}

.notice-item.active .notice-summary {
  font-weight: 600;
  background-color: #fdfdfd;
}

/*   */
.notice-content {
  background-color: #f5f5f5;
  padding: 30px 40px;
  color: #444;
  line-height: 1.6;
  white-space: pre-wrap; /*   */
  font-size: 15px;
  border-top: 1px solid #f0f0f0;
  text-align: left;
}

@media (max-width: 768px) {
  .col-id, .col-date { display: none; } /*  /  */
  .notice-summary { padding: 15px 10px; }
  .col-title { padding: 0 10px; font-size: 14px; }
}