/*  */
.product-form-container {
  display: grid;
  grid-template-columns: 2fr 1fr; /*  2 :  1 */
  gap: 24px;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 40px;
}

@media (max-width: 768px) {
  .product-form-container {
    grid-template-columns: 1fr;
  }
}

/*   */
.form-section {
  margin-bottom: 24px;
  padding: 24px;
  background: #fff;
  border: 1px solid #e0e0e0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  border-radius: 8px;
}

.section-title {
  font-size: 16px;
  font-weight: 700;
  margin: 0 0 20px 0;
  color: #333;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

/*  */
.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.form-page-title {
  font-size: 24px;
  font-weight: 700;
  color: #111;
  margin: 0;
}

/*    */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 14px;
}

.form-input, .form-textarea, .form-select {
  width: 100%;
  padding: 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-textarea {
  min-height: 150px;
  resize: vertical;
}

/*   */
.option-header {
  display: flex;
  gap: 8px;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: bold;
  color: #666;
}

.option-row {
  display: flex;
  gap: 8px;
  margin-bottom: 10px;
  align-items: center;
}

.btn-remove-option {
  background: #ff6b6b;
  color: white;
  border: none;
  border-radius: 4px;
  width: 50px;
  height: 42px;
  cursor: pointer;
}

.btn-add-option {
  width: 100%;
  padding: 10px;
  border: 1px dashed #ccc;
  background: #fff;
  color: #555;
  cursor: pointer;
  margin-top: 10px;
  border-radius: 4px;
}

/*   */
.image-preview {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 8px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  background-color: #f9f9f9;
}

.no-image {
  height: 250px;
  background: #f9f9f9;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #aaa;
  margin-bottom: 10px;
  border: 1px solid #ddd;
}

.image-help-text {
  font-size: 12px;
  color: #888;
  margin-top: 8px;
}

/*  */
.btn-submit {
  width: 150px;
  padding: 12px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
}

.btn-submit:hover {
  opacity: 0.9;
}

/* ===    === */

/*    */
.image-upload-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
  margin-bottom: 10px;
}

/*    */
.image-preview-card {
  position: relative;
  width: 100%;
  padding-bottom: 100%; /* 1:1   */
  border-radius: 8px;
  border: 1px solid #ddd;
  overflow: hidden;
  background-color: #f9f9f9;
  cursor: pointer;
  transition: border-color 0.2s;
}

.image-preview-card:hover {
  border-color: #333;
}

/*     */
.image-preview-card.is-main {
  border: 2px solid #4e54c8; /*   */
}

/*    */
.image-preview-card img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*    ( ) */
.main-badge {
  position: absolute;
  top: 5px;
  left: 5px;
  background-color: #4e54c8;
  color: white;
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 4px;
  font-weight: bold;
  z-index: 2;
}

/*    ( ) */
.new-badge {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background-color: #28a745; /*  */
  color: white;
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 4px;
  font-weight: bold;
  z-index: 2;
}

/*   ( ) */
.btn-remove-image {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 20px;
  height: 20px;
  background-color: rgba(0,0,0,0.6);
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  cursor: pointer;
  z-index: 2;
}

.btn-remove-image:hover {
  background-color: red;
}

/*     */
.no-image-box {
  height: 150px;
  background: #f9f9f9;
  border: 2px dashed #ddd;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #aaa;
  margin-bottom: 10px;
  font-size: 13px;
}