/*   (       ) */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /*    */
  display: flex;
  justify-content: center; /*    */
  align-items: center; /*    */
  z-index: 1000;
}

/*    (    ) */
.popup-content {
  background-color: #fff;
  padding: 24px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

/*   (   ) */
.popup-header {
  display: flex;
  justify-content: space-between; /*       */
  align-items: center;
  border-bottom: 1px solid #eee;
  padding-bottom: 15px;
  margin-bottom: 20px;
}
.popup-header h3 {
  margin: 0;
  font-size: 1.25rem;
}
/*     */
.btn-close {
  background: none;
  border: none;
  font-size: 1.8rem;
  font-weight: 300;
  color: #888;
  cursor: pointer;
  padding: 0;
  line-height: 1;
}

/*    (  ) */
.popup-body {
  text-align: left;
}

/*      (    ) */
.terms-content {
  max-height: 400px; /*    */
  overflow-y: auto;  /*        */
  border: 1px solid #eee;
  padding: 15px;
  background-color: #fdfdfd;
  font-size: 0.9rem;
  color: #333;
  line-height: 1.6;
}

/*      */
.terms-content h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 10px;
}
.terms-content h4:first-child {
  margin-top: 0;
}
.terms-content p {
  margin-bottom: 15px;
}