/* --- 1.   --- */
/*      ,       */
.order-result-page {
  width: 100%;
  max-width: 960px; /*    */
  margin: 40px auto;
  padding: 20px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  color: #333;
}

/*    (/   ) */
.result-icon-wrapper {
  text-align: center;
  margin: 40px 0 20px 0;
}
/*       */
.result-icon {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
}
/*      () */
.result-icon.success {
  background-color: #28a745;
}
/*      (  ,   ) */
.result-icon.fail {
  background-color: #fce8e6;
  color: #d9534f;
  border: 1px solid #f8d7da;
  font-size: 2rem;
}

/*   (   ) */
.result-title {
  font-size: 2rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 8px;
  text-align: center;
}
/*   ( ) */
.result-subtitle {
  font-size: 1rem;
  color: #555;
  margin-bottom: 40px;
  text-align: center;
}

/*     */
.result-box {
  border: 1px solid #eee;
  border-radius: 8px;
  background-color: #fff;
  padding: 24px;
  margin-bottom: 20px;
  text-align: left; 
}
/*     (  ) */
.result-box h2 {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0 0 20px 0;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

/* --- 2. []   --- */
/*     (: ) */
.order-info-box .info-row {
  display: flex;
  justify-content: space-between; /*      */
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 15px;
}
/*  ()   */
.order-info-box .info-row span:first-child {
  color: #888;
}
/*     */
.order-info-box .info-row span:last-child {
  font-weight: 500;
  color: #333;
}
/*       (  ) */
.order-info-box .info-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}
/*        */
.order-info-box .info-total-amount {
  font-size: 1.75rem;
  font-weight: 700;
  color: #333;
}

/* --- 3. []   --- */
/*           */
.step-row {
  display: flex;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #f5f5f5;
}
.step-row:last-child {
  border-bottom: none;
}
/*      */
.step-row-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #f0f0f0;
  color: #888;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  font-size: 1.2rem;
}
/*       */
.step-row-text h3 {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 5px 0;
}
.step-row-text p {
  font-size: 0.9rem;
  color: #666;
  margin: 0;
}

/* --- 4. []   --- */
/*         */
.error-alert-box {
  background-color: #fef7f7;
  border: 1px solid #f8d7da;
  border-radius: 8px;
  padding: 20px;
  display: flex;
  align-items: center;
  color: #721c24; /*     */
  margin-bottom: 20px;
}
/*    */
.error-alert-box .icon {
  font-size: 1.5rem;
  margin-right: 15px;
  color: #d9534f;
}
.error-alert-box p {
  margin: 0;
  line-height: 1.5;
}
/*         */
.error-alert-box strong {
  font-weight: 600;
  display: block;
  margin-bottom: 5px;
}

/* --- 5. [] //  --- */
/*       */
.solution-list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}
/*      */
.solution-item {
  display: flex;
  align-items: center;
  font-size: 0.95rem;
  color: #333;
  margin-bottom: 15px;
}
/*       */
.solution-item .number {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #333; /*    */
  color: #fff;
  font-size: 0.85rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  flex-shrink: 0;
}
/*      */
.support-box, .notification-box {
  padding: 20px;
  border-radius: 8px;
  background-color: #f9f9f9;
  text-align: left;
  line-height: 1.6;
  font-size: 0.9rem;
  color: #555;
  display: flex;
  align-items: flex-start;
  gap: 15px;
}
.support-box .icon {
  font-size: 1.5rem;
  color: #007bff;
}
.support-box h3 {
  margin: 0 0 10px 0;
  font-size: 1.1rem;
  color: #333;
}
.support-box p {
  margin: 0;
}
/*        */
.notification-box {
  background-color: #f8f8f8;
  color: #666;
  font-size: 0.85rem;
}

/* --- 6.  --- */
/*        */
.result-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 30px;
}
/*       */
.result-buttons a {
  padding: 14px 20px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none;
  width: 200px; /*    */
  box-sizing: border-box;
  display: flex;            
  justify-content: center; 
  align-items: center;      
  text-align: center;
  text-decoration: none;
}

/*    ( ) */
.btn-primary-dark {
  color: #fff;
  background-color: #333;
  border: 1px solid #333;
}
/*    ( ) */
.btn-secondary-light {
  color: #333;
  background-color: #fff;
  border: 1px solid #ddd;
}

/* []     (    ) */
.btn-primary-blue {
  color: #fff;
  background-color: #333;
  border: 1px solid #333;
}